.toggle label {
  width: 50px;
  height: 22px;
  background-color: var(--white-general);
  border: 1px solid var(--black-main);
  cursor: pointer;
  border-radius: 100px;
  display: block;
  position: relative;
}

.toggle label:before {
  width: 900px;
  height: 900px;
  z-index: 10;
  opacity: 1;
  pointer-events: none;
  content: "";
  opacity: 0;
  z-index: -999;
  border-radius: 100%;
  margin: auto;
  transition: background-color .3s ease-in-out .2s, opacity .35s ease-in-out .2s, transform .4s ease-in-out;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translate3d(-47%, 0, 0)scale(.1);
}

.toggle label:after {
  width: 14px;
  height: 14px;
  background-color: var(--color-red-accent);
  border: 1px solid var(--black-main);
  content: "";
  border-radius: 100px;
  margin: auto;
  transition: background-color .2s linear, transform .2s ease-in-out .1s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 4px;
}

.toggle input {
  display: none;
}

.toggle input:checked ~ label:after {
  background-color: var(--black-main);
  transform: translate3d(27px, 0, 0);
}

.toggle input:checked ~ label:before {
  opacity: 1;
  transition: background-color .3s ease-in-out, opacity 30ms ease-in-out, transform .4s ease-in-out;
  transform: translate3d(-84%, 23%, 0)scale(1);
}

